<template>
	<div id="invation">
		<div class="bg" style=""></div>
		 <van-nav-bar fixed style="z-index:1001">
		  <div slot="title">邀请好友</div>
		  <van-icon name="arrow-left" slot="left" color="black" @click="$router.go(-1)"/>
		</van-nav-bar>

	  <div id="container">
	  	<div id="bottom">
	  		<!-- <img src="/static/img/invation.png" alt=""> -->
				<img style="width: 100%;height: 100%;" :src="QRcode" alt="">
	  	</div>
	  </div>

	</div>
</template>

<script>
    export default {
      name: "invation",
			data() {
			  return {
			    QRcode:'',
			  }
			},
			created() {
			  this.gain()
			},
      methods: {
        goBack () {
          console.log('goback')
          this.$router.go(-1)
        },
				gain(){
					this.$POST('/user/invite').then(res =>{
						console.log(res);
						if (res.code === 200) {
							this.QRcode =  res.data[0]
						}
					}).catch(res => {
						this.$toast('连接服务器失败!');
						console.log(res);
					})
				}
      },


    }
</script>

<style scoped>
	#container{
		width:100%;
		height:100%;
	}
	#top>img{
		width:100%;
		height:100%;
		position:absolute;
		left:0px;
		top:0px;
		z-index:2;
	}

	#bottom>img{
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0px;
		top: 0px;
		z-index: 0;
	}
</style>



